@import url(https://fonts.googleapis.com/css?family=Anonymous+Pro);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

*{margin:0;padding:0;font-family:sans-serif;box-sizing:border-box;color:white}body{background-color:black}

.container{display:table;width:100%;height:60vh;background-image:linear-gradient(to bottom, rgba(0,0,0,0) 0%, #000 100%),url(/newsSHOW/static/media/city.c678059f.jpg);background-size:cover;overflow:hidden;background-repeat:no-repeat;background-position:50% 50%}

.navbar{width:100%;display:flex;align-items:center}.logo{font-size:calc(10px + 1.5vw);max-width:10px;cursor:pointer;margin:30px 50px;color:#848d31;font-family:'Diplomata SC', cursive}.menu-icon{display:none}nav{flex:1 1;text-align:right}nav ul li{list-style:none;display:inline-block;margin-right:30px}nav ul li a{text-decoration:none;color:#fff;font-size:15px;transition:-webkit-transform 0.25s ease-out;transition:transform 0.25s ease-out;transition:transform 0.25s ease-out, -webkit-transform 0.25s ease-out;font-family:'Bebas Neue', cursive;font-family:'Comfortaa', cursive;font-weight:bold}nav ul li a:hover{color:#848d31;-webkit-transform:scaleX(1);transform:scaleX(1);-webkit-transform-origin:bottom left;transform-origin:bottom left}nav ul li a:after{-webkit-transform:scaleX(1);transform:scaleX(1);-webkit-transform-origin:bottom left;transform-origin:bottom left}@media (max-width: 880px){.menu-icon{margin-right:50px;fill:#fce57e;width:35px;cursor:pointer;display:block}nav ul{width:100%;background-color:#0c0909;opacity:0.5;position:absolute;top:75px;right:0;z-index:2}nav ul li{display:block;margin-top:10px;margin-bottom:10px}nav ul li a{height:35px;display:flex;padding:10px;justify-content:center;color:#fff;font-family:'Bebas Neue', cursive;font-family:'Comfortaa', cursive;text-transform:uppercase}.hidemenu{max-height:0px;transition:all 0.5 ease-out}.showmenu{max-height:200px;transition:all 0.5s ease-out}.hide{display:none}}

.quote{display:flex;justify-content:flex-end;margin-top:40%}.quote h1{color:#848d31;font-family:'Diplomata SC', cursive;font-size:2rem}

html{font-size:10px}img{width:100%;border-radius:10px}.blog-post{width:100%;height:100%;display:flex;align-items:center;justify-content:center;width:100%;max-width:98rem;padding:5rem;background-color:#05192e;box-shadow:0 1.4rem 8rem rgba(0,0,0,0.2);margin:auto}.blog-post__img{min-width:25rem;max-width:35rem;height:30rem;display:flex;align-items:center;justify-content:center;box-shadow:0 1.4rem 8rem rgba(0,0,0,0.2)}.blog-post__img img{width:100%;height:100%;object-fit:cover;display:block;border-radius:.8rem}.blog-post__img::before{content:'';width:100%;height:100%;position:relative;top:0;left:0}span{display:block;color:rgba(0,195,255,0.5);font-size:1.6rem;font-weight:600;margin:.5rem 0}.blog-post__title{font-size:2.5rem;margin:1.5rem 0 2rem;text-transform:uppercase;color:#829cc0;font-family:'Diplomata SC', cursive;font-family:'Kaisei HarunoUmi', serif}.blog-post__text{margin-bottom:3rem;font-size:1.4rem;color:#8e87ac;font-family:'Diplomata SC', cursive;font-family:'Kaisei HarunoUmi', serif;font-family:'Quicksand', sans-serif}.blog-post__cta{display:inline-block;padding:1.5rem 3rem;letter-spacing:1px;text-transform:uppercase;font-size:1.3rem;background-image:linear-gradient(to right, red 0%, blue 100%);text-decoration:none;border-radius:.2rem}.blog-post__cta:hover{background-image:linear-gradient(to right, blue 0%, red 100%)}@media (max-width: 806px){.blog-post{max-width:80rem;padding:2.5rem;flex-direction:column}.blog-post__img{min-width:100%;max-width:100%}.blog-post__cta{display:flex;justify-content:center;margin:auto}}

.line-1{color:#848d31;padding:25px;position:relative;top:50%;margin:0 auto;font-size:150%;text-align:center;white-space:nowrap;overflow:hidden;font-family:'Diplomata SC', cursive}.anim-typewriter{-webkit-animation:typewriter 7s steps(44) 1s 100 normal both,blinkTextCursor 1000ms steps(44) infinite normal;animation:typewriter 7s steps(44) 1s 100 normal both,blinkTextCursor 1000ms steps(44) infinite normal}@-webkit-keyframes typewriter{from{width:0}to{width:34em}}@keyframes typewriter{from{width:0}to{width:34em}}@-webkit-keyframes blinkTextCursor{from{border-right-color:rgba(133,29,29,0.75)}to{border-right-color:transparent}}@keyframes blinkTextCursor{from{border-right-color:rgba(133,29,29,0.75)}to{border-right-color:transparent}}

